<script>
import { NavTabsCard } from "@/components";
import NavPills from "./NavPillsSection";

export default {
  components: {
    NavTabsCard,
    NavPills
  },
  data() {
    return {
      textarea: "",
      modifiedText: "",
      list: null,
      listLoading: false,
      img: {
        save: require("@/assets/img/aids.png") //
      },
      activeName: "1"
    };
  },
  methods: {
    onSubmit() {
      if (this.textarea !== "") {
        this.listLoading = true;
        fetch(`http://localhost:5000/text2signal/${this.textarea}`, {
          method: "get",
          headers: { "Content-Type": "application/json" }
        })
          .then(res => {
            return { data: res };
          })
          .then(response => {
            response.data.json().then(res => {
              this.list = res.data;
              this.modifiedText = res.corrected;
              // console.log("text2signal:", res);
              this.listLoading = false;
            });
          });
      } else {
        this.$message.error(" :");
      }
    },
    open() {
      this.$alert("", "", {
        confirmButtonText: "",
        callback: action => {
          this.$message({
            type: "info",
            message: ``
          });
        }
      });
    }
  }
};
</script>

